// BLOCKS *****************************************************

.wrapper {
    @include wrapper();
}

.row-flex {
    @include row-flex();
}

html {
    background: #CDCDCD;
    font-size: 10px;
}

body {
    max-width: 1920px;
    min-width: 320px;
    margin: 0 auto;
    position: relative;
    background-color: #ffffff;
}

a {
    cursor: pointer;
}

.contact {
    text-align: right;

    &_phone {
        font-size: 1.6rem;
        line-height: 2.2rem;
        font-family: "MullerMedium";
    }

    &_adress {
        font-size: 1.4rem;
        line-height: 1.4rem;
        font-family: "MullerMedium";
        opacity: 0.6;
    }
}

.feedback-form {
    &_title {
        font-family: MullerMedium, sans-serif;
        font-size: 2.4rem;
        line-height: 24px;
        letter-spacing: 0.02em;
        margin-bottom: 8px;
    }

    &_subtitle,
    &_subtitle a{
        font-family: MullerMedium, sans-serif;
        font-size: 1.6rem;
        letter-spacing: 0.02em;
        color: rgba(0, 0, 0, 0.6);
    }
     &_subtitle{
          margin-bottom: 40px;
     }
    

    &_fields {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
    }

    &_inputs {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    input {
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        width: 220px;
        height: 52px;
        padding-left: 18px;

        &::placeholder {
            font-family: Muller;
            font-size: 1.6rem;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: rgba(0, 0, 0, 0.6);
        }
    }

    &_caption {
        font-family: MullerBold, sans-serif;
        font-size: 1.6rem;
        line-height: 16px;
        letter-spacing: 0.02em;
        margin-bottom: 10px;
    }

    &_agreement {
        font-size: 1rem;
        line-height: 120%;
        letter-spacing: 0.06em;
        color: rgba(0, 0, 0, 0.6);
    }

    @media(max-width:1440px) {
        &-agreement {
            width: 100%;
            text-align: center;
        }

        &_fields {
            justify-content: center;
        }

        &_field {
            width: 46%;
            margin-bottom: 20px;
            margin-right: 30px;
        }

        button {
            margin-top: 25px;
            width: 100%;
        }

        &-order {
            flex-direction: column;
            max-width: 278px;
            margin-top: 0px;
        }

        input {
            width: 100%;
        }
    }

    @media(max-width:780px) {
        &_field {
            margin-right: 10px;
        }

        &_inputs {
            justify-content: center;
        }
    }

    @media(max-width:630px) {
        &_field {
            width: 100%;
        }
    }
}

//HEADER********************************************

.header {
    width: 100%;
    padding-bottom: 120px;

    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        &_left {
            display: flex;
            align-items: center;
        }

        &_right {
            display: flex;
            align-items: center;
        }

    }

    &_inside {
        background: url("../images/header-bg.jpg") no-repeat;
        //background-size: contain;
    }

    &_top {
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        height: 116px;
        background: #FFFFFF;
        padding-top: 30px;
    }

    &_nav {
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: 30px;

        @media(max-width: 670px) {
            height: 0px;
        }
    }

    &_title {
        margin-bottom: 29px;
        @include font-size(7.2rem);
    }

    &_text {
        font-family: "MullerMedium";
        font-size: 2.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.02em;
        margin-bottom: 5.0rem;

    }

    &_content {
        @include size(5);
        @include padding-top(16.3rem);
    }

    &_phone {
        color: #000000;
    }

    &_btn {
        margin-bottom: 217px;
    }

    &_top-btn {
        margin-left: 35px;
        @media(max-width: 420px){
            margin-left: 0;
        }
    }

    &_logo {
        margin-right: 80px;
        //background:url("../images/logo-blue-desktop.png") no-repeat;
        width: 166px;
        height: 56px;

    }

    &_contact {
        @media(max-width: 670px) {
            display: none;
        }

        &-mobile {
            display: none;
        }
    }

}

@media(max-width: 1300px) {
    .header {
        &_btn {
            margin-bottom: 100px;
        }

        &_top {
            height: auto;
            padding-bottom: 20px;
        }

        .wrapper {
            &_left {
                width: 100%;
                justify-content: space-between;
            }

            &_right {
                width: 100%;
                justify-content: flex-end;
            }
        }
    }
}

@media(max-width: 900px) {
    .header {
        &_nav {
            margin-right: 0;
        }

        &_top {
            height: auto;
            padding-bottom: 20px;
        }

        &_logo {
            align-self: center;
            margin-right: 0;
        }

        &_content {
            width: 100%;
        }

        .wrapper {
            flex-direction: column;
            &_left {
                flex-wrap: wrap;
                flex-direction: column;
            }

            &_right {
                width: auto;
                justify-content: flex-start;
            }
        }
    }
}

@media(max-width:670px) {
    .header {
        .wrapper {
            flex-direction: row;
            position: relative;
        }

        .wrapper_left {
            width: auto;
        }

        &_logo {
            margin-left: 95px;
        }

    }

}

@media(max-width:500px) {
    .header {
        .wrapper {
            flex-direction: row;
            position: relative;
        }

        .wrapper_left {
            width: auto;
        }

        &_logo {
            margin-left: 35px;
        }

        &_content h1 {
            line-height: 45px;
        }
    }

}

.advantages {
    @include row-flex();
    align-items: center;
    height: 180px;

    &_item {
        display: flex;
        align-items: start;
        justify-content: space-around;
        @include col();
        @include size(3);
    }

    &_title {
        font-family: "MullerMedium", "sans-serif";
        //font-size: 2.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.02em;
        @include font-size(2.4rem)
    }

    &_text {
        font-family: "MullerMedium", sans-serif;
        font-size: 1.4rem;
        line-height: 120%;
        letter-spacing: 0.02em;
        color: rgba(0, 0, 0, 0.6);
    }

    &_description {
        width: 65%;
    }
}

.advantages-wrapper {
    background: #FFFFFF;
    box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
    border-radius: 5px;
}

@media (max-width:1470px) {
    .advantages {
        height: 200px;

        &_item {
            width: calc(50% - 30px);
        }
    }
}

@media (max-width:1300px) {
    .advantages {
        &_description {
            width: 100%;
            margin-left: 20px;
        }
    }
}

@media (max-width:880px) {
    .advantages {
        height: auto;
        padding-top: 25px;
        padding-left: 20px;

        &_item {
            width: 100%;
            margin-bottom: 25px;
        }

    }
}

/********MENU BLOCK****************/
.menu {
    li {
        display: block;
        flex-shrink: 0;

        &:hover {
            opacity: 1;
        }
    }

    &_toggle {
        background-color: transparent;
        display: none;
        height: 18px;
        width: 24px;
        position: relative;
        label{
            width: 100%;
            height: 100%;
            z-index: 10;
            background:transparent;
            position: absolute;
            top:0;
            left:0;
            cursor: pointer;
        }

        @media (max-width:670px) {
            display: block;
            position: absolute;
            left: 15px;
            top: 10px;
        }
    }

    .last {
        margin-right: 0px;
    }

    &_item-1 {
        position: relative;
    }

    a {
        color: #000000;
        opacity: 0.6;
        display: block;
        font-size: 1.6rem;
        font-family: "MullerMedium";
        line-height: 22px;
        letter-spacing: 0.15px;
        transition: all .2s ease-in-out;
        &:hover {
            opacity: 1;
        }
    }
}

.menu_item-1 {
    a {
        display: flex;
        align-items: center;
    }
    span {
        display: block;
        color: $primary;
    }
}

.menu_item-1:hover {
    background-color: #ffffff;
    span {
        transform: rotateZ(180deg);
    }

    .menu_drop-1 {
        display: block;
    }
}

.menu_drop-1 {
    z-index: 1000;
    position: absolute;
    top: 30px;
    width: 210px;
    background-color: #ffffff;
    display: none;
    box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
    border-radius: 5px;
     @media (max-width:670px) {
         left: 130px;
         top: 0px;
     }

    li {
        padding-bottom: 5px;
        margin-right: 0;
        padding-left: 20px;
        border-bottom: solid 1px #EDEDEE;
        transition: all .2s ease-in-out;

        &:hover {
            background: $primary;

            a {
                color: white;
            }
        }
    }

    li:last-child {
        border-bottom-width: 0px;
    }

    a {
        line-height: 36px;
    }
}

svg#menu {
    width: 24px;
}

.menu_list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    width: 630px;
    @media(max-width:670px) {
        display: block;
        position: absolute;
        top: 75px;
        height: auto;
        width: 0%;
        z-index: 1001;
        background-color: #ffffff;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;
        left: -1000px;
        transition: width 1s;
        &>li {
            height: auto !important;
            margin-right: 0 !important;
            padding-bottom: 5px;
            padding-top: 5px;
            padding-left: 30px;
            border-bottom: solid 1px #EDEDEE;
            transition: all .2s ease-in-out;
            a {
                line-height: 36px;
            }
             &:hover {
                 border-bottom: 1px solid #EDEDEE !important;
                 ;
             }
        }
    }
    &>li {
        margin-right: 10px;
        height: 30px;

        &:hover {
            border-bottom: 2px solid $primary;
        }
    }
}
input[type="checkbox"] {
    position: absolute;
    top:-1000;
}

input[type="checkbox"]:checked~.menu_list {
    display: block;
    left: 0;
    width: 100%;
}

/************ END OF MENU BLOCK**********/


// SERVICE BLOCK*****************************************
.service {
    @include wrapper();
    height: 660px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 140px;
    position: relative;

    &_tooth {
        position: absolute;
        top: -200px;
        right: -15%;
        @media(max-width: 1920px){
            display: none;
        }
    }

    &_about {
        width: 57%;
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &_feedback {
        width: 40.5%;
    }

    &_subtitle {
        font-family: "MullerBold";
        font-size: 24px;
        line-height: 87%;
        letter-spacing: 0.02em;
    }

    &_text {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.15px;
        color: #F5F5F5;
    }

    &_discription,
    &_price-discription {
        font-size: 1.8rem;
        line-height: 22px;
        letter-spacing: 0.02em;
        color: rgba(0, 0, 0, 0.6);
    }

    &_price-discription li {
        line-height: 32px;
    }

    &_danger {
        display: flex;

        &-text {
            height: auto;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            margin-left: 20px;

            p:nth-of-type(1) {
                font-family: MullerBold;
                font-size: 2.4rem;
                line-height: 2.4rem;
                letter-spacing: 0.02em;
            }

            p:nth-of-type(2) {
                font-family: MullerMedium;
                font-size: 1.6rem;
                line-height: 1.9rem;
                letter-spacing: 0.02em;
                color: rgba(0, 0, 0, 0.6);
            }
        }
    }

    &_price {
        background-size: cover;
        background-color: $primary;
        height: 135px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 50px;
        margin-bottom: 20px;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;

        p:nth-of-type(1) {
            font-size: 2.4rem;
            line-height: 24px;
            letter-spacing: 0.02em;
            color: #FFFFFF;
            background-color: transparent;
            margin-bottom: 8px;
        }

        p:nth-of-type(2) {
            font-size: 1.6rem;
            line-height: 19px;
            letter-spacing: 0.02em;
            color: rgba(255, 255, 255, 0.6);
            background-color: transparent;
        }
    }

    &_form {
        background: #FFFFFF;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;
        padding: 40px;

        hr {
            width: 100%;
            height: 1px;
            background: #D6D6D6;
            transform: matrix(1, 0, 0, -1, 0, 0);
            margin-bottom: 39px;
        }

        &-field {
            margin-bottom: 20px;
        }

        &-order {
            display: flex;
            margin-top: 50px;
        }

        &-agreement {
            width: 57%;
            padding-top: 5px;

            a {
                color: $primary;
            }
        }
    }

    &_btn {
        width: 184px;
        margin-right: 20px;
    }


}



@media(max-width:1440px) {
    .service {
        height: auto;
        flex-direction: column;
        justify-content: flex-start;
        margin-bottom: 0;
        
        &_title,
        &_subtitle,
        &_discription{
            margin-bottom: 25px;
        }
        &_about {
            width: 100%;
            height: 57%;
            align-items: center;
        }

        &_feedback {
            width: 100%;
