}


//************CARIES BLOCK**************************
.caries {
    background: url("../images/caries-bg.jpg") no-repeat;
    background-size: 100% 100%;
    padding-top: 80px;
    padding-bottom: 80px;
    //@extend: 80px 0;

    .wrapper {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
    }

    &_questions {
        background: #FFFFFF;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;
        width: 66%;
        @include padding-top(10rem);
        padding-right: 10px;
        padding-bottom: 10px;
        @include padding-left(14.5rem);
        margin-left: -50px;
        z-index: 1;

        h3 {
            margin-left: 25px;
            margin-bottom: 23px;
        }
    }

    &_img {
        width: 41.3%;
        z-index: 10;
        position: relative;
    }
}

.accordion {
    height: 373px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 6px;
        background: #CDD4E0;
        border-radius: 27px;
    }

    &::-webkit-scrollbar-thumb {
        background: #0545BA;
        border-radius: 27px;
    }

    &_item {
        margin-right: 25px;
        margin-left: 25px;
        margin-bottom: 11px;

        &--active {
            .accordion_text {
                max-height: 135px;
                padding-top: 22px;
                padding-left: 26px;
                padding-bottom: 22px;
                transition: max-height .4s linear;

                p {
                    max-height: 100%;
                    transition: max-height .4s linear;
                }
            }

            .accordion_item-header {
                background: #0545BA;
                color: #ffffff;

                span {
                    transform: rotate(180deg);
                    transition: transform .4s linear;
                }
            }
        }

        &-header {
            display: flex;
            align-items: center;
            padding-left: 25px;
            background: #ffffff;
            border-radius: 5px;
            border: 2px solid #0545BA;
            color: $primary;
            height: 52px;
        }

        h5 {
            margin-left: 10px;
        }

        span {
            font-size: 2.5rem;
        }
    }

    &_text {
        width: 100%;
        padding: 0;
        background: #FFFFFF;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        max-height: 0;
        overflow: hidden;

        p {
            width: 65%;
            text-align: justify;
            line-height: 17px;
            max-height: 0;
            overflow: hidden;
        }
    }
}

@media(max-width:1300px) {
    .accordion {
        &_text p {
            width: 95%;
        }
    }
}

@media(max-width:970px) {
    .accordion {
        height: auto;
        overflow-y: auto;
    }
}

@media(max-width:970px) {
    .caries {
        background: none;

        .wrapper {
            flex-direction: column;
        }

        &_img {
            width: 100%;
            padding-left: 20px;
            padding-right: 20px;
        }

        &_btn-prev {
            left: -0px;
        }

        &_btn-next {
            right: -0px;
        }

        &_questions {
            margin-top: 23px;
            margin-right: 20px;
            margin-left: 20px;
            width: 100%;
            padding-left: 0;
            padding-top: 0;
            padding-top: 40px;
            width: auto;
        }
    }
}

@media(max-width:460px) {
    .accordion_item h5 {
        font-size: 1.2rem;
    }
}

//  *************STAGES SECTION *******
.stages {
    background: url(../images/stages-bg.jpg) no-repeat;
    background-size: contain;
    max-width: 1140px;
    margin: 0 auto;
    padding-top: 100px;
    position: relative;
    @include margin-bottom(140px);
    &_tooth{
        position: absolute;
        top:0;
        left:-50px;
    }

    &_title,
    &_subtitle {
        text-align: center;
    }
    &_subtitle {
        margin-top: 30px;
        margin-bottom: 6rem;
    }
    &_list {
        display: flex;
        flex-wrap: wrap;

        li {
            list-style-type: none;
            position: relative;
            width: 360px;
            margin-right: 30px;

            &:nth-of-type(2) {
                margin-right: 390px;
                margin-bottom: 80px;
            }

            &:nth-of-type(3) {
                margin-left: 390px;
            }

            &:nth-of-type(4) {
                margin-right: 0px;
                margin-bottom: 70px;
            }

            &:nth-of-type(5) {
                margin-bottom: 65px;
            }

            &:nth-of-type(6) {
                margin-right: 390px;
            }

            &:nth-of-type(9) {
                margin-right: 0px;
            }
        }

        // *****Points COLORS*******

        li:nth-of-type(-n+2) span {
            border-color: #1091A5;

            &::before,
            &::after {
                background: #1091A5;
            }
        }

        li:nth-of-type(n+3):nth-of-type(-n+4) span {
            border-color: #136AA2;

            &::before,
            &::after {
                background: #136AA2;
            }
        }

        li:nth-of-type(n+5):nth-of-type(-n+6) span {
            border-color: #182C9F;

            &::before,
            &::after {
                background: #182C9F;
            }
        }

        li:nth-of-type(n+7):nth-of-type(-n+9) span {
            border-color: #09134A;

            &::before,
            &::after {
                background: #09134A;
            }
        }

        // ******Lines styles*********

        li:nth-of-type(1) span {
            &::after {
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                width: 358px;
                height: 2px;
            }
        }

        li:nth-of-type(2) span {
            &::after {
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                width: 376px;
                height: 2px;
            }
        }

        li:nth-of-type(3) span {
            display: block;
            margin: 0 auto;
            width: 32px;
            height: 32px;
            position: relative;

            &::after {
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                width: 376px;
                height: 2px;
            }

            &:nth-of-type(2) {
                margin-top: -32px;

                &::before {
                    content: '';
                    position: absolute;
                    bottom: 50%;
                    transform-origin: 0 50% 0;
                    transform: rotate(-180deg);
                    width: 374px;
                    height: 2px;
                }
            }
        }

        li:nth-of-type(4) span {
            &::after {
                left: 50%;
                bottom: 100%;
                height: 330px;
                width: 2px;
                transform: translateX(-50%);
            }
        }

        li:nth-of-type(5) span {
            display: block;
            margin: 0 auto;
            width: 32px;
            height: 32px;
            position: relative;

            &::after {
                left: 50%;
                bottom: 100%;
                height: 468px;
                width: 2px;
                transform: translateX(-50%);
            }

            &:nth-of-type(2) {
                margin-top: -32px;

                &::before {
                    content: '';
                    position: absolute;
                    bottom: 50%;
                    left: 100%;
                    width: 375px;
                    height: 2px;
                }
            }
        }

        li:nth-of-type(6) span {
            &::after {
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                width: 376px;
                height: 2px;
            }
        }

        li:nth-of-type(7) span {
            &::after {
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                width: 360px;
                height: 2px;
            }
        }

        li:nth-of-type(8) span {
            &::after {
                left: 100%;
                top: 50%;
                transform: translateY(-50%);
                width: 360px;
                height: 2px;
            }
        }

        li:nth-of-type(9) span {
            &::after {
                left: 50%;
                bottom: 100%;
                height: 375px;
                width: 2px;
                transform: translateX(-50%);
            }
        }
    }

    // ******* Points styles **************
    &_point {
        display: block;
        position: relative;
        width: 32px;
        height: 32px;
        background: #ffffff;
        border: 2px solid;
        border-radius: 50%;
        margin: 0 auto;

        &::before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 18px;
            height: 18px;
            border-radius: 50%;
        }

        &::after {
            content: '';
            position: absolute;
        }
    }

    &_item {
        border: 2px solid #0545BA;
        box-shadow: 0px 4px 32px rgba(15, 27, 97, 0.06);
        border-radius: 5px;
        margin-top: 6.0rem;
        padding-top: 3.0rem;
        padding-left: 3.0rem;
        padding-bottom: 3.0rem;
        padding-right: 2.0rem;

        h4 {
            color: $primary;
        }

        p {
            font-family: MullerMedium;
            line-height: 20px;
        }
    }

    @media(max-width:1200px) {
        padding-left: 15px;
        padding-right: 15px;
        li:nth-of-type(1)::after,
        li:nth-of-type(2)::after,
        li:nth-of-type(3)::after,
        li:nth-of-type(4)::after,
        li:nth-of-type(5)::after,
        li:nth-of-type(6)::after,
        li:nth-of-type(7)::after,
        li:nth-of-type(8)::after,
        li:nth-of-type(9)::after {
            position: absolute;
            width: 24px;
            height: 24px;
            background-color: $primary;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: white;
            top: 5px;
            right: 50%;
        }

        li:nth-of-type(1)::after {
            content: "1";
        }

        li:nth-of-type(2)::after {
            content: "2";
        }

        li:nth-of-type(3)::after {
            content: "3";
        }

        li:nth-of-type(4)::after {
            content: "4";
        }

        li:nth-of-type(5)::after {
            content: "5";
        }

        li:nth-of-type(6)::after {
            content: "6";
        }

        li:nth-of-type(7)::after {
            content: "7";
        }

        li:nth-of-type(8)::after {
            content: "8";
        }

        li:nth-of-type(9)::after {
            content: "9";
        }

        &_point,
        li:nth-of-type(1) span::after,
        li:nth-of-type(2) span::after,
        li:nth-of-type(4) span::after,
        li:nth-of-type(6) span::after,
        li:nth-of-type(7) span::after,
        li:nth-of-type(8) span::after,
        li:nth-of-type(9) span::after {
            display: none;
        }

        li:nth-of-type(3) span {
            display: none;

            &:nth-of-type(2) {
                &::before {
                    display: none;
                }
            }
        }

        li:nth-of-type(5) span {
            display: none;

            &:nth-of-type(2) {
                display: none;
            }
        }

        &_item {
            margin: 0 20px 0 0;
        }

        &_list {
            justify-content: space-between;

            li {
                margin-right: 0px;
                margin-bottom: 10px;

                &:nth-of-type(2) {
                    margin-bottom: 10px;
                    margin-right: 0px;
                }

                &:nth-of-type(3) {
                    margin-left: 0px;
                }

                &:nth-of-type(4) {
                    margin-bottom: 10px;
                    margin-right: 0px;
                }

                &:nth-of-type(5) {
                    margin-bottom: 10px;
                }

                &:nth-of-type(6) {
                    margin-right: 0px;
                }
            }
        }
    }

    @media(max-width:1115px) {
        &_list {
            justify-content: center;
        }
    }
     @media(max-width:970px) {
         padding-top: 0;

     }
     @media(max-width:820px) {
