            flex-wrap: wrap;
            height: auto;

            li {
                width: 25%;
                display: block;
                margin-bottom: 2.5rem;
                text-align: left;

                @media(max-width: 400px) {
                    width: 50%;
                    margin-right: 0;
                }
            }
        }

        &_reg {
            order: 1;
            width: 50%;
            margin-right: 0;
            justify-content: flex-end;
            margin-bottom: 3rem;

            @media(max-width: 400px) {
                justify-content: left;
            }
        }
    }
}